// ** React Imports
import { useEffect, useState, Fragment } from 'react'
// ** Third Party Components
import Flatpickr from 'react-flatpickr'
import { X, Plus, Trash } from 'react-feather'
import { DSCList, addDsc, deleteDigitalSignature } from '../store/index'
import { useForm, useFieldArray, Controller } from "react-hook-form"
//import moment from 'moment'
import * as yup from "yup"
import { yupResolver } from "@hookform/resolvers/yup"

// ** Reactstrap Imports
import { Row, Col, Card, Label, Button, CardBody, CardText, Input, FormFeedback } from 'reactstrap'

// ** Styles
import 'react-slidedown/lib/slidedown.css'
import '@styles/react/libs/react-select/_react-select.scss'
import '@styles/react/libs/flatpickr/flatpickr.scss'
import '@styles/base/pages/app-invoice.scss'
import { useDispatch, useSelector } from 'react-redux'
import { activeOrganizationid, orgUserId } from '@src/helper/sassHelper'
import UILoader from '@components/ui-loader'
import Spinner from '@components/spinner/Loading-spinner'
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
import moment from 'moment'
const activeOrgId = activeOrganizationid()
const userId = orgUserId()
const clientId = localStorage.getItem('clientId')

const AddCard = ({ updateRefresh }) => {

  const MySwal = withReactContent(Swal)

  const [block, setBlock] = useState(false)
  const Loader = () => {
    return (
      <Fragment>
        <Spinner />
      </Fragment>
    )
  }

  const phoneRegExp = /^[0-9\- ]{10,10}$/
  const dispatch = useDispatch()
  const store = useSelector(state => state.digitalsignature)
  
  const schema = yup.object().shape({
    clientId: yup.string().default(clientId),
    rows: yup.array().of(
      yup.object().shape({
        name: yup.string().required("Please Enter Name"),
        createdBy: yup.string().default(userId),
        organizationId: yup.string().default(activeOrgId),
        email: yup.string().email().required("Please Enter Email"),
        contact: yup.string().matches(phoneRegExp, { message: 'Phone number is not valid', excludeEmptyString: true }),
        issuedDate: yup.number().required("Please Enter Issued Date"),
        expiryDate: yup.number().required("Please Enter Expiry Date"),
        password: yup.string().min(5, "Password length should be 5 or above.")
      })
    )
  })

  const { handleSubmit, formState: { errors }, control } = useForm({
    resolver: yupResolver(schema),
    defaultValues: schema.cast()
  })
  const { fields, append, remove } = useFieldArray({ name: 'rows', keyName: 'rowid', control })
  const onSubmit = async data => {
    setBlock(true)
    const temp = [...data.rows]
    const finaldata = temp.map((obj) => {
      obj['issuedDate'] = moment.unix(obj['issuedDate']).startOf('d').valueOf()
      obj['expiryDate'] = moment.unix(obj['expiryDate']).startOf('d').valueOf()

      return obj
    })
    data.rows = finaldata
    await dispatch(addDsc(data))    
    updateRefresh(true)
    remove()
  }

  const addItem = (() => {
    append({ clientId, name: '', email: '', contact: '', issuedDate: '', expiryDate: '', password: '' })

    control._formValues.rows.forEach((obj, key) => {
      control._formValues.rows[key].clientId = clientId
    })

  })

  const deletefun = (id) => {

    return MySwal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes, delete it!',
      customClass: {
        confirmButton: 'btn btn-primary',
        cancelButton: 'btn btn-outline-danger ms-1'
      },
      buttonsStyling: false
    }).then(async (result) => {
      if (result.value) {
        await dispatch(deleteDigitalSignature(id))
        MySwal.fire({
          icon: 'success',
          title: 'Deleted!',
          text: 'DSC has been deleted.',
          customClass: {
            confirmButton: 'btn btn-success'
          }
        })
        return true
      } else if (result.dismiss === MySwal.DismissReason.cancel) {
        return false
      }
    })
  }

  const removeItem = async (ind) => {
    const tempid = control._formValues.rows[ind].id
    let flg
    if (tempid !== undefined) {
      flg = await deletefun(tempid)
    }

    if (flg) {
      remove(ind)
    }

  }

  const getClientInfo = async () => {
    remove()
    await dispatch(DSCList(clientId))
  }

  useEffect(() => {
    store.DSCLists.forEach((obj) => {
      const data = {}
      data['id'] = obj.id
      data['name'] = obj.name
      data['createdBy'] = userId
      data['email'] = obj.email
      data['organizationId'] = activeOrgId
      data['clientId'] = clientId
      data['contact'] = obj.contact
      data['issuedDate'] = obj.issueddate ? String(obj.issueddate / 1000) : null
      data['expiryDate'] = obj.expirydate ? String(obj.expirydate / 1000) : null
      data['password'] = obj.password || ''

      append(data)
    })
    if (store.DSCLists.length === 0) {
      addItem()
    }

  }, [store.DSCLists])

  useEffect(() => {
    getClientInfo()
  }, [])

  return (
    <UILoader blocking={block} loader={<Loader />}>
      <form onSubmit={handleSubmit(onSubmit)}>

          {/* Header */}

          <CardBody className='invoice-padding invoice-product-details'>
            {fields.map((item, i) => (
              <div key={item.rowid} className='repeater-wrapper'>
                <Row >
                  <Col className='d-lg-flex product-details-border position-relative pe-0' sm='12'>
                    <Row className='w-100 pe-lg-0 pe-1 py-2'>
                      <Col className='mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-2 col-sm-12'>
                        <CardText className='col-title required mb-md-50 mb-0'>Name</CardText>
                        <Controller
                          control={control}
                          id='dsc_list_name'
                          name={`rows[${i}].name`}
                          render={({ field }) => (
                            <Input type='text' invalid={errors.rows?.[i]?.name && true} {...field} />
                          )}
                        />
                        {errors.rows?.[i]?.name && <FormFeedback>{errors.rows?.[i]?.name.message}</FormFeedback>}
                      </Col>
                      <Col className='my-lg-0 my-2 col-lg-2 col-sm-12'>
                        <CardText className='col-title required mb-md-50 mb-0'>Email</CardText>
                        <Controller
                          control={control}
                          id='dsc_list_email'
                          name={`rows[${i}].email`}
                          render={({ field }) => (
                            <Input type='email' invalid={errors.rows?.[i]?.email && true} {...field} />
                          )}
                        />
                        {errors.rows?.[i]?.email && <FormFeedback>{errors.rows?.[i]?.email.message}</FormFeedback>}
                      </Col>
                      <Col className='my-lg-0 my-2' lg='2' sm='12'>
                        <CardText className='col-title required mb-md-50 mb-0'>Mobile</CardText>
                        <Controller
                          control={control}
                          id='dsc_list_contactNumber'
                          name={`rows[${i}].contact`}
                          render={({ field }) => (
                            <Input type='number' invalid={errors.rows?.[i]?.contact && true} {...field} />
                          )}
                        />
                        {errors.rows?.[i]?.contact && <FormFeedback>{errors.rows?.[i]?.contact.message}</FormFeedback>}
                      </Col>
                      <Col className='my-lg-0 mt-2' lg='2' sm='12'>
                        <CardText className='col-title required mb-md-50 mb-0'>Issued Date</CardText>
                        <Controller
                          control={control}
                          id='dsc_list_issuedDate'
                          name={`rows[${i}].issuedDate`}
                          render={({ field }) => (
                            <Flatpickr
                              value={field.value}
                              onChange={(date, dateStr) => { field.onChange(dateStr) }}
                              options={{ altInput: true, altFormat: "F j, Y", dateFormat: "U" }}
                              className='form-control invoice-edit-input date-picker'
                            />
                          )}
                        />
                        {errors.rows?.[i]?.issuedDate && <FormFeedback>{errors.rows?.[i]?.issuedDate.message}</FormFeedback>}
                      </Col>
                      <Col className='my-lg-0 mt-2' lg='2' sm='12'>
                        <CardText className='col-title required mb-md-50 mb-0'>Expiry Date</CardText>
                        <Controller
                          control={control}
                          id='dsc_list_expiryDate'
                          name={`rows[${i}].expiryDate`}
                          render={({ field }) => (
                            <Flatpickr
                              value={field.value}
                              onChange={(date, dateStr) => { field.onChange(dateStr) }}
                              options={{ altInput: true, altFormat: "F j, Y", dateFormat: "U" }}
                              className='form-control invoice-edit-input date-picker'
                            />
                          )}
                        />
                        {errors.rows?.[i]?.expiryDate && <FormFeedback>{errors.rows?.[i]?.expiryDate.message}</FormFeedback>}
                      </Col>
                      <Col className='my-lg-0 mt-2' lg='2' sm='12'>
                        <CardText className='col-title required mb-md-50 mb-0'>Password</CardText>
                        <Controller
                          control={control}
                          id='dsc_list_password'
                          name={`rows[${i}].password`}
                          render={({ field }) => (
                            <Input type='text' invalid={errors.rows?.[i]?.password && true} {...field} />
                          )}
                        />
                        {errors.rows?.[i]?.password && <FormFeedback>{errors.rows?.[i]?.password.message}</FormFeedback>}
                      </Col>
                    </Row>
                    <div className='d-lg-flex justify-content-center invoice-product-actions py-50 px-25'>
                      <Trash size={18} className='cursor-pointer' onClick={() => removeItem(i)} />
                    </div>
                  </Col>
                </Row>
              </div>
            ))}

            <Row className='mt-1'>
              <Col sm='12' className='px-0'>
                <Button color='primary' size='sm' className='btn-add-new' onClick={() => addItem()}>
                  <Plus size={14} className='me-25'></Plus> <span className='align-middle'>Add Item</span>
                </Button>
              </Col>
            </Row>

          </CardBody>
          {/* /Header */}
        
        <Card>
          <CardBody>
            <div className='modal-footer border-0'>
              <Button color='warning' outline onClick={() => { updateRefresh(true) }}>
                Cancel
              </Button>
              <Button color='primary' type="submit" >
                Save
              </Button>
            </div>
          </CardBody>
        </Card>
      </form>
    </UILoader>
  )
}

export default AddCard
