// ** React Imports
import { Fragment, useState, useEffect, forwardRef } from 'react'
import { useParams } from 'react-router-dom'
import Avatar from '@components/avatar'
import { Can } from '@src/utility/context/Can.js'
// ** Store & Actions
import { getData, deleteDigitalSignaturelist, updateDSCLocation } from '../store'
import { useDispatch, useSelector } from 'react-redux'
// ** Third Party Components
import ReactPaginate from 'react-paginate'
import UILoader from '@components/ui-loader'
import Spinner from '@components/spinner/Loading-spinner'
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
import AddDSC from '@src/views/apps/digital-signature/add/AddCard.js'
import EditDSC from '@src/views/apps/digital-signature/edit/EditCard.js'
import { orgUserId } from '@src/helper/sassHelper'
import toast from 'react-hot-toast'
const userId = orgUserId()

import {
  Badge,
  Row,
  Col,
  Card,
  Input,
  Button,
  Table,
  Modal,
  ModalHeader,
  ModalBody,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem
} from 'reactstrap'
// ** Styles

import '@styles/react/libs/react-select/_react-select.scss'
import moment from 'moment'
import { Edit, MoreVertical, Trash } from 'react-feather'

// ** Table Header
const CustomHeader = ({ handleFilter, searchTerm, updateRefreshList }) => {
  const [formModal, setFormModal] = useState(false)
  const { id } = useParams()
  const updateRefresh = (val) => {
    if (val) {
      setFormModal(false)
      updateRefreshList(true)
    }
  }

  return (
    <div className='d-flex flex-wrap flex-md-nowrap p-2 p-md-1 my-75'>
      <Modal isOpen={formModal} toggle={() => setFormModal(!formModal)} className='modal-dialog-centered modal-dsc'>

        <ModalHeader toggle={() => setFormModal(!formModal)}>Digital Signature</ModalHeader>
        <ModalBody>
          <AddDSC updateRefresh={updateRefresh} />
        </ModalBody>
      </Modal>
      <Col className='col-12 col-md-4 d-flex align-items-center ps-1 pe-0'>
        <div className='d-flex align-items-center w-100'>
          <h5 htmlFor='rows-per-page'>Digital Signatures</h5>
        </div>
      </Col>
      <Col className='col-12 col-md-8 d-flex align-items-sm-center justify-content-xl-end justify-content-start flex-xl-nowrap flex-wrap flex-sm-row flex-column pe-xl-1 p-0 mt-xl-0 mt-1'
      >
        {!id &&
          <Fragment>

            <Col md={3} sm={12} lg={3} className='p-25'>
              <Input
                id='search-invoice'
                className=''
                type='text'
                placeholder='Search'
                value={searchTerm}
                onChange={e => handleFilter(e.target.value)}
              />
            </Col>
          </Fragment>
        }
        <Col className='d-flex justify-content-start justify-content-lg-end align-items-center table-header-actions col-12 col-md-3'>

          <Button className='add-new-user' color='primary' onClick={() => { setFormModal(!formModal) }}>
            Add
          </Button>
        </Col>
      </Col>
    </div>
  )
}

const DigitalSignatureList = () => {
  // ** Store Vars
  const dispatch = useDispatch()
  const store = useSelector(state => { return state.digitalsignature })
  
  // ** States
  const [sort] = useState('desc')
  const [searchTerm, setSearchTerm] = useState('')
  const [currentPage, setCurrentPage] = useState(1)
  const [sortColumn] = useState('createdon')
  const rowsPerPage = 25
  const [currentLocation] = useState({ id: '', name: 'All' })
  const [formModal, setFormModal] = useState(false)
  const [selectedDSC, setSelectedDSC] = useState(null)
  const locationArr = ['', 'Office', 'With Client']

  const statusObj = [
    '',
    'light-primary',
    'light-success',
    'light-danger'
  ]

  const statusArr = [
    '',
    "Active",
    "Inactive",
    "Expired"
  ]


  const renderClient = row => {

    return (
      <Avatar
        initials
        className='me-1'
        color={'light-primary'}
        content={row.name || ''}
      />
    )

  }

  const dateFormat = (value) => {
    if (value.length === 13) {
      return moment(value, 'x').format("MMM DD, YYYY")
    } else {
      return moment.unix(value).format("MMM DD, YYYY")
    }

  }

  // ** Bootstrap Checkbox Component
  const BootstrapCheckbox = forwardRef((props, ref) => (
    <div className='form-check'>
      <Input type='checkbox' ref={ref} {...props} />
    </div>
  ))

  const [block, setBlock] = useState(false)
  const Loader = () => {
    return (
      <Fragment>
        <Spinner />
      </Fragment>
    )
  }
  // ** Get data on mount
  useEffect(async () => {
    setBlock(true)
    await dispatch(
      getData({
        sort,
        sortColumn,
        q: searchTerm,
        page: currentPage,
        perPage: rowsPerPage,
        location: currentLocation.id
      })
    )
    setBlock(false)
  }, [dispatch])

  // ** Function in get data on page change
  const handlePagination = async page => {
    setBlock(true)
    await dispatch(
      getData({
        sort,
        sortColumn,
        q: searchTerm,
        perPage: rowsPerPage,
        page: page.selected + 1,
        location: currentLocation.id
      })
    )
    setBlock(false)
    setCurrentPage(page.selected + 1)
  }

  // ** Function in get data on search query change
  const handleFilter = async val => {
    setSearchTerm(val)
    setBlock(true)
    await dispatch(
      getData({
        sort,
        q: val,
        sortColumn,
        page: currentPage,
        perPage: rowsPerPage,
        location: currentLocation.id
      })
    )
    setBlock(false)
  }

  // ** Custom Pagination
  const CustomPagination = () => {
    const count = Number(Math.ceil(store.total / rowsPerPage))

    return (
      <ReactPaginate
        previousLabel={''}
        nextLabel={''}
        pageCount={count || 1}
        activeClassName='active'
        forcePage={currentPage !== 0 ? currentPage - 1 : 0}
        onPageChange={page => handlePagination(page)}
        pageClassName={'page-item'}
        nextLinkClassName={'page-link'}
        nextClassName={'page-item next'}
        previousClassName={'page-item prev'}
        previousLinkClassName={'page-link'}
        pageLinkClassName={'page-link'}
        containerClassName={'pagination react-paginate justify-content-end my-2 pe-1'}
      />
    )
  }

  // ** Table data to render
  const dataToRender = () => {
    const filters = {
      q: searchTerm
    }

    const isFiltered = Object.keys(filters).some(function (k) {
      return filters[k].length > 0
    })

    if (store.data.length > 0) {
      return store.data
    } else if (store.data.length === 0 && isFiltered) {
      return []
    } else {
      return store.allData.slice(0, rowsPerPage)
    }
  }

  const MySwal = withReactContent(Swal)

  const deletefun = (id) => {

    return MySwal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes, delete it!',
      customClass: {
        confirmButton: 'btn btn-primary',
        cancelButton: 'btn btn-outline-danger ms-1'
      },
      buttonsStyling: false
    }).then(async (result) => {
      if (result.value) {
        await dispatch(deleteDigitalSignaturelist(id))
        MySwal.fire({
          icon: 'success',
          title: 'Deleted!',
          text: 'Digital Signature has been deleted.',
          customClass: {
            confirmButton: 'btn btn-success'
          }
        })
      }
    })
  }

  const updateRefreshList = async (val) => {
    if (val) {
      setBlock(true)
      await dispatch(
        getData({
          sort,
          q: searchTerm,
          sortColumn,
          page: currentPage,
          perPage: rowsPerPage,
          location: currentLocation.id
        })
      )
      setBlock(false)
    }
  }

  const updateLocation = async (val, id) => {
    const obj = { id, location: val, updatedBy: userId }
    await dispatch(updateDSCLocation(obj))
    toast.success('Digital Signature Location Update Successfully.', { position: 'bottom-right' })
  }

  const setFreshList = () => {
    setFormModal(false)
    setSelectedDSC(null)

  }

  const editDSC = (val) => {

    setFormModal(true)
    setSelectedDSC(val)
  }

  return (
    <Fragment>
      <UILoader blocking={block} loader={<Loader />}>
        <Card className='overflow-hidden'>
          <Row>
            <CustomHeader
              searchTerm={searchTerm}
              rowsPerPage={rowsPerPage}
              handleFilter={handleFilter}
              updateRefreshList={updateRefreshList}
            />

            <Modal isOpen={formModal} toggle={() => setFormModal(!formModal)} className='modal-dialog-centered modal-dsc'>

              <ModalHeader toggle={() => setFormModal(!formModal)}>Digital Signature</ModalHeader>
              <ModalBody>
                <EditDSC selectedDSC={selectedDSC} setFreshList={setFreshList} />
              </ModalBody>
            </Modal>

            <Table responsive className='table tabel-bordered table-hover'>
              <thead>
                <tr>
                  <th>Id</th>
                  <th>Name</th>
                  <th>Contact</th>
                  <th>Issued Date</th>
                  <th>Expiry Date</th>
                  <th>Location</th>
                  <th>Password</th>
                  <th>Status</th>
                  <th>Actions</th>
                </tr>
              </thead>
              <tbody>{

                dataToRender().map((row, k) => {
                  return (
                    <tr key={k}>
                      <td>
                        <div className='d-flex justify-content-left align-items-center'>
                          <span className='text-capitalize fw-bolder font-small-3'>{row.uniqueidentity}</span>
                        </div>
                      </td>
                      <td>
                        <div className='d-flex justify-content-left align-items-center font-small-5'>
                          {renderClient(row)}
                          <div className='d-flex flex-column'>
                            <span className='fw-bolder font-small-5'>{row.name}</span>
                            <small className='text-truncate mb-0 font-small-3'>{row.email}</small>
                          </div>
                        </div>
                      </td>
                      <td>
                        {row.contact}
                      </td>
                      <td className='font-small-3'>
                        {dateFormat(row.issueddate)}
                      </td>
                      <td className='font-small-3'>
                        {dateFormat(row.expirydate)}
                      </td>
                      <td>
                        <Badge className='text-capitalize' color={statusObj[row.dsclocation]} pill>{locationArr[row.dsclocation]}</Badge>
                      </td>
                      <td>
                        {row.password}
                      </td>
                      <td>
                        <Badge className='text-capitalize' color={statusObj[row.status]} pill>
                          {statusArr[row.status]}
                        </Badge>
                      </td>
                      <td>
                        <div className='column-action d-flex align-items-center'>
                          <Can I={'delete'} a={'digital-signature'}>
                            <Col tag={'a'} href='/'
                              onClick={e => {
                                e.preventDefault()
                                deletefun(row.id)
                              }}
                            >
                              <Trash
                                className='cursor-pointer ms-1 mt-0' size={16} />
                            </Col>
                            <Col tag={'a'} href='/'
                              onClick={e => {
                                e.preventDefault()
                                editDSC(row)
                              }}
                            >
                              <Edit
                                className='cursor-pointer ms-1 mt-0' size={16} />
                            </Col>
                          </Can>
                          <UncontrolledDropdown>
                            <DropdownToggle tag='div' className='btn btn-sm'>
                              <MoreVertical size={14} className='cursor-pointer' />
                            </DropdownToggle>
                            <DropdownMenu>
                              <DropdownItem
                                tag='a'
                                href='/'
                                className='w-100'
                                onClick={e => {
                                  e.preventDefault()
                                  updateLocation(1, row.id)
                                }}
                              >
                                <span className='align-middle'>Office</span>
                              </DropdownItem>

                              <DropdownItem
                                tag='a'
                                href='/'
                                className='w-100'
                                onClick={e => {
                                  e.preventDefault()
                                  updateLocation(2, row.id)
                                }}
                              >
                                <span className='align-middle'>With Client</span>
                              </DropdownItem>

                            </DropdownMenu>
                          </UncontrolledDropdown>
                        </div>
                      </td>
                    </tr>
                  )
                })
              }</tbody>
            </Table>
            <CustomPagination />
          </Row>
        </Card>
      </UILoader>
    </Fragment>
  )
}

export default DigitalSignatureList