// ** React Imports
import { useEffect, useState, Fragment } from 'react'
import { Link, useParams } from 'react-router-dom'
// ** Third Party Components
import Flatpickr from 'react-flatpickr'
import { DSCList, updateDSC } from '../store/index'
import { useForm, Controller } from "react-hook-form"
import * as yup from "yup"
import { yupResolver } from "@hookform/resolvers/yup"

// ** Reactstrap Imports
import { Row, Col, Card, Label, Button, CardBody, CardText, Input, FormFeedback } from 'reactstrap'

// ** Styles
import 'react-slidedown/lib/slidedown.css'
import '@styles/react/libs/react-select/_react-select.scss'
import '@styles/react/libs/flatpickr/flatpickr.scss'
import '@styles/base/pages/app-invoice.scss'
import { useDispatch } from 'react-redux'
import { activeOrganizationid, orgUserId } from '@src/helper/sassHelper'
import UILoader from '@components/ui-loader'
import Spinner from '@components/spinner/Loading-spinner'
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
import moment from 'moment'
const userId = orgUserId()
const activeOrgId = activeOrganizationid()
const EditCard = ({ selectedDSC, setFreshList }) => {

  const [block, setBlock] = useState(false)
  const Loader = () => {
    return (
      <Fragment>
        <Spinner />
      </Fragment>
    )
  }

  const MySwal = withReactContent(Swal)
  const phoneRegExp = /^[0-9\- ]{10,10}$/
  const dispatch = useDispatch()

  const { id } = useParams()
  const schema = yup.object().shape({
    clientId: yup.string().default(id),
    rows: yup.array().of(
      yup.object().shape({
        name: yup.string().required("Please Enter Name"),
        updatedBy: yup.string().default(userId),
        organizationId: yup.string().default(activeOrgId),
        email: yup.string().email().required("Please Enter Email"),
        contact: yup.string().matches(phoneRegExp, { message: 'Phone number is not valid', excludeEmptyString: true }),
        issuedDate: yup.number().required("Please Enter Issued Date"),
        expiryDate: yup.number().required("Please Enter Expiry Date"),
        password: yup.string()
      })
    )
  })

  const { handleSubmit, formState: { errors }, control, setValue } = useForm({
    resolver: yupResolver(schema),
    defaultValues: schema.cast()
  })

  const onSubmit = async data => {
    setBlock(true)
    data['issuedDate'] = moment.unix(data['issuedDate']).startOf('d').valueOf()
    data['expiryDate'] = moment.unix(data['expiryDate']).startOf('d').valueOf()
    await dispatch(updateDSC(data))
    setFreshList(true)
  }

  const getClientInfo = async () => {
    if (id !== undefined) {
      await dispatch(DSCList(id))
    }
  }

  useEffect(() => {

    if (selectedDSC) {
      const obj = selectedDSC
      setValue('id', obj.id)
      setValue('name', obj.name)
      setValue('email', obj.email)
      setValue('organizationId', activeOrgId)
      setValue('clientId', id)
      setValue('contact', obj.contact)
      setValue('issuedDate', obj.issueddate.length === 13 ? String(obj.issueddate / 1000) : obj.issueddate)
      setValue('expiryDate', obj.expirydate.length === 13 ? String(obj.expirydate / 1000) : obj.expirydate)
      setValue('password', obj.password)
    }
  }, [selectedDSC])

  useEffect(() => {

    setValue('clientId', id)
    getClientInfo()
  }, [])

  return (
    <UILoader blocking={block} loader={<Loader />}>
      <form onSubmit={handleSubmit(onSubmit)}>
        <Card className='invoice-preview-card'>
          {/* Header */}
          <CardBody className='invoice-padding invoice-product-details'>

            <div className='repeater-wrapper'>
              <Row >
                <Col className='d-lg-flex product-details-border position-relative pe-0' sm='12'>
                  <Row className='w-100 pe-lg-0 pe-1 py-2'>
                    <Col className='mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-2 col-sm-12'>
                      <CardText className='col-title required mb-md-50 mb-0'>Name</CardText>
                      <Controller
                        control={control}
                        id='dsc_list_name'
                        name={`name`}
                        render={({ field }) => (
                          <Input type='text' invalid={errors?.name && true} {...field} />
                        )}
                      />
                      {errors?.name && <FormFeedback>{errors?.name.message}</FormFeedback>}
                    </Col>
                    <Col className='my-lg-0 my-2 col-lg-2 col-sm-12'>
                      <CardText className='col-title required mb-md-50 mb-0'>Email</CardText>
                      <Controller
                        control={control}
                        id='dsc_list_email'
                        name={`email`}
                        render={({ field }) => (
                          <Input type='email' invalid={errors?.email && true} {...field} />
                        )}
                      />
                      {errors?.email && <FormFeedback>{errors?.email.message}</FormFeedback>}
                    </Col>
                    <Col className='my-lg-0 my-2' lg='2' sm='12'>
                      <CardText className='col-title required mb-md-50 mb-0'>Mobile</CardText>
                      <Controller
                        control={control}
                        id='dsc_list_contactNumber'
                        name={`contact`}
                        render={({ field }) => (
                          <Input type='number' invalid={errors?.contact && true} {...field} />
                        )}
                      />
                      {errors?.contact && <FormFeedback>{errors?.contact.message}</FormFeedback>}
                    </Col>
                    <Col className='my-lg-0 mt-2' lg='2' sm='12'>
                      <CardText className='col-title required mb-md-50 mb-0'>Issued Date</CardText>
                      <Controller
                        control={control}
                        id='dsc_list_issuedDate'
                        name={`issuedDate`}
                        render={({ field }) => (
                          <Flatpickr
                            value={field.value}
                            onChange={(date, dateStr) => { field.onChange(dateStr) }}
                            options={{ altInput: true, altFormat: "F j, Y", dateFormat: "U" }}
                            className='form-control invoice-edit-input date-picker'
                          />
                        )}
                      />
                      {errors?.issuedDate && <FormFeedback>{errors?.issuedDate.message}</FormFeedback>}
                    </Col>
                    <Col className='my-lg-0 mt-2' lg='2' sm='12'>
                      <CardText className='col-title required mb-md-50 mb-0'>Expiry Date</CardText>
                      <Controller
                        control={control}
                        id='dsc_list_expiryDate'
                        name={`expiryDate`}
                        render={({ field }) => (
                          <Flatpickr
                            value={field.value}
                            onChange={(date, dateStr) => { field.onChange(dateStr) }}
                            options={{ altInput: true, altFormat: "F j, Y", dateFormat: "U" }}
                            className='form-control invoice-edit-input date-picker'
                          />
                        )}
                      />
                      {errors?.expiryDate && <FormFeedback>{errors?.expiryDate.message}</FormFeedback>}
                    </Col>
                    <Col className='my-lg-0 mt-2' lg='2' sm='12'>
                      <CardText className='col-title required mb-md-50 mb-0'>Password</CardText>
                      <Controller
                        control={control}
                        id='dsc_list_password'
                        name={`password`}
                        render={({ field }) => (
                          <Input type='text' invalid={errors?.password && true} {...field} />
                        )}
                      />
                      {errors?.password && <FormFeedback>{errors?.password.message}</FormFeedback>}
                    </Col>
                  </Row>
                </Col>
              </Row>
            </div>
          </CardBody>
          {/* /Header */}
        </Card>
        <Card>
          <CardBody>
            <div className='modal-footer border-0'>
              <Button color='warning' outline onClick={() => setFreshList(true)}>
                Cancel
              </Button>
              <Button color='primary' type="submit" >
                Save
              </Button>
            </div>
          </CardBody>
        </Card>
      </form>
    </UILoader>
  )
}

export default EditCard
